        </p>
        <h4>AUTHORIZATION</h4>
        <p>
          Setting a webhook authorization allows you to ensure that POST requests sent to the payload URL are from Nodriza. When you set a authorization, you'll receive the AUTHORIZATION header in the webhook POST request.
        </p>
        <h4>CONTENT TYPE</h4>
        <p>
          Webhooks can be delivered using different content types:
          <ul class="mt-2 ul-classic">
            <li>The application/json content type will deliver the JSON payload directly as the body of the POST request.</li>
            <li>The application/x-www-form-urlencoded content type will send the JSON payload as a form parameter called payload.</li>
          </ul>
        </p>
        <h4>ACTIVE</h4>
        <p>
          By default, webhook deliveries are "Active." You can choose to disable the delivery of webhook payloads by deselecting "Active."
        </p>
        <h4>EVENTS</h4>
        <p>
          Events are at the core of webhooks. These webhooks fire whenever a certain action is taken on the nodriza platform, which your server's payload URL intercepts and acts upon.
