<template>
  <v-container fluid>
    <v-row>
      <v-col>
        <h1>Get Started</h1>
      </v-col>
    </v-row>
    <v-row>
      <v-col>
        The Nodriza Data API lets you incorporate functions normally executed into your own account, this reference guide explains how to use the API to perform all of these operations.
      </v-col>
    </v-row>
    <v-row>
      <v-col cols="12">
        <h3>Nodriza SDK</h3>
        <a target="_blank" href="https://github.com/nodriza-io/nodriza">Install</a>
        <pre v-highlightjs>
          <code class="javascript elevation-3 block">
          import Nodriza from 'nodriza'

          const params = {
            hostname: 'xxx.nodriza.io',
            accessToken: 'xxxxxxx-xxxx-xxxx-xxxx-xxxxxxxxxx'
          }

          const nodriza = new Nodriza(params)

          nodriza.api.user.me((err, profile) => {
            if (err) return console.log('->>> err:', err)
            console.log('->>> profile:', profile)
          })
          </code>
        </pre>
      </v-col>
      <v-col cols="12">
        <h3>Nodriza API REST</h3>
        <pre v-highlightjs>
          <code class="javascript elevation-3 block">
          var myHeaders = new Headers();
          myHeaders.append("authorization", "bearer xxxxxxx-xxxx-xxxx-xxxx-xxxxxxxxxx");
          myHeaders.append("Content-Type", "application/json");
          
          var body = { 
            title: "Demo Proposal"
          }
          
          var raw = JSON.stringify(body);

          var requestOptions = {
            method: 'POST',
            headers: myHeaders,
            body: raw,
            redirect: 'follow'
          };

          fetch("https://{nodriza_account}.nodriza.io/v1/{model_name}", requestOptions)
            .then(response => response.json())
            .then(result => console.log(result))
            .catch(error => console.log('error', error));
          </code>
        </pre>
