<template>
  <v-container fluid>
    <v-row>
      <v-col>
        <h1>{{ $route.meta.title }}</h1>
      </v-col>
    </v-row>
    <v-row>
      <v-col>
        <b>NOTE:</b> callback function returns two params (error, data)
      </v-col>
    </v-row>
    <v-row>
      <v-col>
        <v-tabs v-model="tab">
          <v-tab v-if="usageTabComponent">Usage</v-tab>
          <v-tab>Schema</v-tab>
          <v-tab>SDK</v-tab>
          <v-tab>API REST</v-tab>
        </v-tabs>
        <v-tabs-items v-model="tab">
          <v-tab-item v-if="usageTabComponent"><component v-bind:is="usageTabComponent"></component></v-tab-item>
          <v-tab-item><Schema v-if="isShow" :model="model" /></v-tab-item>
          <v-tab-item><Methods v-if="isShow" :model="model" /></v-tab-item>
          <v-tab-item>
            <br>
            <template v-if="existsModel(model)">
              <p>
                Nodriza uses <a target="_blank" href="https://swagger.io/">swagger</a> as her API documentation engine.
              </p>
              <p>
                To see how all this works, go to Api Explorer section into your Nodriza account, or copy and paste this link in your browser
                <br><a target="_blank" :href="'https://account_name.nodriza.io/app/preferences/api-explorer/'+model">
                  https://<span class="red--text">{nodriza_account}</span>.nodriza.io/app/preferences/api-explorer/{{model}}.
                </a>
              </p>
            </template>
            <template v-else>
              <p>Not available in Nodriza SDK</p>
            </template>
          </v-tab-item>
        </v-tabs-items>
      </v-col>
    </v-row>
  </v-container>
</template>

<script>
import Webhook from '../usage/Webhook'
import Product from '../usage/Product'
import CustomObject from '../usage/CustomObject'

export default {
  computed: {
    model () {
      return this.$route?.meta?.model
    },
    usageTabComponent () {
      const models = {
        webhook: Webhook,
        product: Product,
        customObject: CustomObject,
      }
      if (models[this.model]) return models[this.model]
      return false
    },
  },
  mounted () {
    console.log(this)
  },
  created () {
    this.$emitter.$on('changeRoute', () => {
      this.isShow = false
      setTimeout(() => {
        this.isShow = true
      }, 1)
    })
  },
  methods: {
    existsModel (model) {
      return this?.$sdk?.schema[model] != undefined
    }
  },
  data () {
    return {
      tab: null,
      isShow: true,
    }
  },
}
</script>