<template>
  <div>
    <template v-if="schema">
      <br>
      <h1>Properties</h1>
      <p>The following table defines the properties that appear in this resource:</p>
    </template>
    
    <v-simple-table v-if="schema">
      <template v-slot:default>
        <tbody>
          <template v-for="(val, key) in schema">
            <tr :key="key">
              <td class="td-title"><b>{{ key }}</b></td>
              <td>
                <b>{{ val.type || 'string' }}</b>
                <br>{{ val.description }}
                <template v-if="val.enum">
                  <br><b>Enum:</b>
                  <ul class="list">
                    <li v-for="item in val.enum" :key="item">{{ item }}</li>
                  </ul>
                </template>
              </td>
            </tr>
          </template>
        </tbody>
      </template>
    </v-simple-table>
    <br>
    <h1>Resource representation</h1>
    <p>The JSON structure below shows the format of an <code>{{model}}</code> resource:</p>
    <code>{{ jsonString }}</code>
  </div>
</template>

<script>
export default {
  props: {
    model: {
      type: String,
      required: true
    }
  },
  computed: {
    jsonString () {
      return JSON.stringify(this.json || {}, null, 2)
    }
  },
  methods: {
    async load (model) {
      this.reset()
      this.json = await this.getJson(model)
      const schema = this?.$sdk?.schema[model]
      if (!schema) return
      const sortedKeys = Object.keys(schema).sort()
      this.schema = sortedKeys.reduce((acc, key) => {
        return { ...acc, [key]: schema[key] }
      }, {})
    },
    async getJson (model) {
      try {
        return await require(`../../samples/${model}.js`)?.default
      } catch (err) {
        console.error(err)
        return {}
      }
    },
    reset () {
      this.json = null
      this.schema = null
    }
  },
  mounted () {
    this.load(this.model)
  },
  data () {
    return {
      json: null,
      schema: null
    }
  }
}
</script>

<style scoped>
  .td-title {
    background-color: #eeeeee;
  }
  .list {
    padding-left: 24px !important;
    list-style: inherit;
  }
</style>