<template>
  <v-container fluid>
    <v-row>
      <v-col>
        <h1>Reference</h1>
      </v-col>
    </v-row>
    <v-row>
      <v-col>
        Lorem ipsum dolor sit amet, consectetur adipisicing elit. Laboriosam a unde corrupti consequatur tempore doloribus at error eveniet omnis quia exercitationem amet placeat, cum sit harum voluptatibus quibusdam nisi perferendis!
      </v-col>
    </v-row>
    <v-row>
      <v-col>
        <Indice :items="sidebar" />
      </v-col>
    </v-row>
  </v-container>
</template>

<script>
import { mapMutations, mapGetters } from 'vuex'

export default {
  data () {
    return {
      sidebar: [],
    }
  },
  methods: {
    ...mapGetters(['getSidebar']),
    ...mapMutations(['setCurrentView']),
  },
  mounted () {
    this.sidebar = []
    this.setCurrentView('reference')
    this.getSidebar()?.map(item => this.sidebar.push(item))
  }
}
</script>