import Vue from 'vue'
import VueRouter from 'vue-router'
import Support from '@/views/Support.vue'
import Store from '@/store/index.js'
import { getReferenceRoute } from '@/plugins/modelRoutes'

Vue.use(VueRouter)

const baseRoutes = [
  {
    path: '/',
    redirect: 'guide'
  },
  {
    path: '/support',
    name: 'support',
    component: Support
  }
]

const router = new VueRouter({
  routes: baseRoutes.concat(getReferenceRoute())
})

router.beforeEach((to, from, next) => {
  Store.commit('setCurrentView', Vue.prototype.$getView())
  next()
})

export default router
