<template>
  <v-navigation-drawer v-model="drawer" :clipped="$vuetify.breakpoint.lgAndUp" app>
    <v-list v-if="sidebar">
      <template v-for="item in sidebar">
        <v-subheader v-if="item.heading" :key="item.heading" >
          {{ item.heading }}
        </v-subheader>
        <v-list-item v-if="!item.heading" :key="item.text" link @click="goTo(item.routeName)">
          <v-list-item-content>
            <v-list-item-title>
              {{ item.text }}
            </v-list-item-title>
          </v-list-item-content>
        </v-list-item>
      </template>
    </v-list>
  </v-navigation-drawer>
</template>

<script>
  import { mapState } from 'vuex'

  export default {
    props: {
      source: String,
    },
    computed: {
      // ...mapGetters(['getSidebar']),
      ...mapState({
        sidebar: state => {
          return state.sidebar
        }
      })
    },
    data: () => ({
      dialog: false,
      drawer: null,
      items: [],
    }),
    methods: {
      goTo (routeName) {
        this.$router.push({ name: routeName })
        this.$emitter.$emit('changeRoute')
      },
      toggle () {
        this.drawer = !this.drawer
      }
    },
    created () {
      
    } 
  }
</script>

