import Vue from 'vue'
// import VueHighlightJS from 'vue-highlightjs'

// import 'highlight.js/lib/languages/javascript.js'
// import 'highlight.js/styles/highlight.css'
// import 'highlight.js/styles/monokai-sublime.css'

import Nodriza from 'nodriza'
import AppMain from './AppMain.vue'
import vuetify from './plugins/vuetify';
import highlight from './plugins/highlight';
import store from './store'
import router from './router'
import Indice from './components/Indice'
import Methods from './components/reference/Methods'
import Schema from './components/reference/Schema'

Vue.config.productionTip = false

Vue.prototype.$getView = function () {
  return window.location?.hash?.slice(2)?.split('-')[0] || ''
}

Vue.prototype.$capitalize = function (str) {
  if (!str || typeof str !== 'string') return ''
  return str.charAt(0).toUpperCase() + str.slice(1)
} 

Vue.component('Indice', Indice)
Vue.component('Schema', Schema)
Vue.component('Methods', Methods)

Vue.use(function (vm) {
  vm.prototype.$emitter = new Vue()
  vm.prototype.$sdk = new Nodriza({ hostname: 'xxx' })
})

new Vue({
  vuetify,
  highlight,
  router,
  store,
  render: h => h(AppMain)
}).$mount('#app')
